.MAR {
    position: fixed;
    background: lightgrey;
    width: 25%;
    height: 40%;
    top: 50%;
    left: 57%;
    transform: translate(-50%,-50%);
    font: 12px Myriad,Helvetica,Tahoma,Arial,clean,sans-serif;
}

.MARCancel {
    width: 10px;
    height: 10px;
    float: right;
    margin-right: 5px;
    cursor: pointer;
}

.MARsearch{
    display: block;
    margin:auto;

}

.MARresults{
    display:block;
    margin:auto;
    margin-top:20px;
    max-width: 400px;
}

.MARresultsDetails{
    border: red solid 1px;
}